import {
  MatSnackBar,
  MatSnackBarHorizontalPosition,
  MatSnackBarVerticalPosition,
} from '@angular/material/snack-bar';
import { DataService } from './../../services/data.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { ProducatDetailsComponent } from './../producat-details/producat-details.component';
import {
  Component,
  ElementRef,
  Input,
  OnInit,
  Renderer2,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
const images = './assets/';

const image = (path) => images + path;
@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ProductComponent implements OnInit {
  @Input() product: any;
  @Input() products: any;
  // @ViewChild('cardBtn') input: ElementRef<HTMLInputElement>;
  cartNumber = 0;
  cartItems: any = [];
  // products = [
  //   {
  //     id: 1,
  //     name: 'aaa',
  //     price: 21,
  //     image: image('1.jpg')
  //   },
  //   {
  //     id: 2,
  //     name: 'bbbb',
  //     price: 21,
  //     image: image('1.jpg')
  //   },
  //   {
  //     id: 3,
  //     name: 'cccccc',
  //     price: 21,
  //     image: image('1.jpg')
  //   },
  //   {
  //     id: 4,
  //     name: 'ddddd',
  //     price: 21,
  //     image: image('1.jpg')
  //   },
  //   {
  //     id: 5,
  //     name: 'rrrr',
  //     price: 21,
  //     image: image('1.jpg')
  //   },
  //   {
  //     id: 6,
  //     name: 'tttttt',
  //     price: 21,
  //     image: image('1.jpg')
  //   },
  // ];
  count = 0;
  toggle = true;
  status = 'Enable';
  horizontalPosition: MatSnackBarHorizontalPosition = 'start';
  verticalPosition: MatSnackBarVerticalPosition = 'bottom';
  constructor(
    public dialog: MatDialog,
    private data: DataService,
    private renderer: Renderer2,
    private snackBar: MatSnackBar
  ) {
    this.data.cartRefresh$.subscribe((info: any) => {
      this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
      this.count = info;
    });
  }

  ngOnInit(): void {
    this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
    // this.check();

    // this.cartItemFunc();
  }
  // check(): void {
  //   this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
  // }
  cartItemFunc(): void {
    if (localStorage.getItem('cart') != null) {
      this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
      this.count = this.cartItems.length;
    }
  }
  openDetails(id: number): void {
    let productData: any;
    const productsData = this.products;
    console.log(this.products);

    productsData.forEach((product: any) => {
      if (product._id === id) {
        productData = product;
      }
    });
    this.dialog.open(ProducatDetailsComponent, {
      data: { products: productData },
    });
  }

  addToCart(product: any): void {
    this.toggle = false;
    this.status = 'Disable';
    console.log(product);
    const cartDataNull = localStorage.getItem('cart');
    const id = product._id;
    if (cartDataNull === null) {
      const getStoredData: any = [];
      getStoredData.push(product);
      localStorage.setItem('cart', JSON.stringify(getStoredData));
      product.status = this.status;
      this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
    } else {
      let index = -1;
      for (let i = 0; i < this.cartItems.length; i++) {
        if (id === this.cartItems[i].id) {
          this.cartItems[i].cartProdQnt = product.cartProdQnt;
          index = i;
          break;
        }
      }
      if (index === -1) {
        this.cartItems.push(product);
        localStorage.setItem('cart', JSON.stringify(this.cartItems));
      } else {
        localStorage.setItem('cart', JSON.stringify(this.cartItems));
      }
    }
    this.snackBar.open(` ( ${product.name} )   Added Successfully`, 'End now', {
      duration: 3000,
      panelClass: ['panel', 'main-color'],
      horizontalPosition: this.horizontalPosition,
      verticalPosition: this.verticalPosition,
    });
    this.cartNumberFunc();
  }

  cartNumberFunc(): void {
    let cartValue: [] = [];
    cartValue = JSON.parse(localStorage.getItem('cart') || '[]');
    this.cartNumber = cartValue.length;
    this.data.cartRefresh$.next(this.cartNumber);
    console.log('cart Number:' + this.cartNumber);
  }
}
