import { MatDialog } from '@angular/material/dialog';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from 'src/app/services/data.service';
import { UserDataComponent } from '../user-data/user-data.component';
import { MatSnackBar, MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition } from '@angular/material/snack-bar';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class CartComponent implements OnInit {
  totalAmount = 0;
  cartItems: any = [];
  order: any = {};
  cartNumber = 0;
  user: any;
  horizontalPosition: MatSnackBarHorizontalPosition = 'start';
  verticalPosition: MatSnackBarVerticalPosition = 'bottom';
  constructor(private router: Router, private data: DataService, private dialog: MatDialog, private snackBar: MatSnackBar) {
    this.data.cartRefresh$.subscribe((info: any) => {
      this.user = JSON.parse(localStorage.getItem('user') || '[]');
    });
  }

  ngOnInit(): void {
    this.user = JSON.parse(localStorage.getItem('user') || '[]');
    this.checkCart();
    this.loadCart();
    this.userRefresh();
    this.cartNumberFunc();
  }

  submitOrder(): void {
    this.dialog.open(UserDataComponent, {
      // width: '70%',
      // panelClass: 'modal',
    });

  }

  userRefresh() {
    const user = JSON.parse(localStorage.getItem('user') || '[]');
    this.data.cartRefresh$.next(user);
  }

  checkCart(): void {
    this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
    if (localStorage.getItem('customer') !== null) {
      this.order = this.cartItems;
    }
  }

  loadCart(): void {
    if (localStorage.getItem('cart')) {
      this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
      this.totalAmount = this.cartItems.reduce((acc: any, val: any) => {
        return acc + val.price * val.cartProdQnt;
      }, 0);
      // this.user.price = this.totalAmount;
    }
  }

  incrementProduct(prodId: any, quantity: number): void {
    // tslint:disable-next-line:prefer-for-of
    for (let i = 0; i < this.cartItems.length; i++) {
      if (this.cartItems[i].id === prodId) {
        this.cartItems[i].cartProdQnt = quantity + 1;
      }
    }
    localStorage.setItem('cart', JSON.stringify(this.cartItems));
    this.loadCart();
  }

  decreaseProduct(prodId: any, quantity: number): void {
    // tslint:disable-next-line:prefer-for-of
    for (let i = 0; i < this.cartItems.length; i++) {
      if (this.cartItems[i].id === prodId) {
        this.cartItems[i].cartProdQnt = quantity - 1;
      }
    }
    localStorage.setItem('cart', JSON.stringify(this.cartItems));
    this.loadCart();
  }



  removeAllCart(): void {
    localStorage.removeItem('cart');
    this.router.navigate(['/']);
  }

  removeOneItem(productId: any): void {
    this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
    for (let i = 0; i < this.cartItems.length; i++) {
      if (this.cartItems[i].id === productId) {
        this.snackBar.open(
          ` ( ${this.cartItems[i].name} )   Added Successfully`,
          'End now',
          {
            duration: 3000,
            panelClass: ['panel', 'main-color'],
            horizontalPosition: this.horizontalPosition,
            verticalPosition: this.verticalPosition,
          }
        );
        this.cartItems.splice(i, 1);
        localStorage.setItem('cart', JSON.stringify(this.cartItems));
        this.loadCart();
        // this.checkCart();
        this.cartNumberFunc();
      }
    }
  }
  cartNumberFunc(): void {
    let cartValue: [] = [];
    cartValue = JSON.parse(localStorage.getItem('cart') || '[]');
    this.cartNumber = cartValue.length;
    this.data.cartRefresh$.next(this.cartNumber);
    console.log('cart Number:' + this.cartNumber);
  }

}
