import { DataService } from './../../services/data.service';
import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-producat-details',
  templateUrl: './producat-details.component.html',
  styleUrls: ['./producat-details.component.scss']
})
export class ProducatDetailsComponent implements OnInit {
  cartNumber = 0;
  cartItems: any = [];
  count = 0;
  constructor(@Inject(MAT_DIALOG_DATA) public data: any, private dataService: DataService) {
    this.dataService.cartRefresh$.subscribe((info: any) => {
      this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
      this.count = info;
    });
  }


  ngOnInit() {
    console.log(this.data);
    this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
  }

  cartItemFunc(): void {
    if (localStorage.getItem('cart') != null) {
      this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
      this.count = this.cartItems.length;
    }
  }

  addToCart(product: any): void {

    console.log(product);
    const cartDataNull = localStorage.getItem('cart');
    const id = product.id;
    if (cartDataNull === null) {
      const getStoredData: any = [];
      getStoredData.push(product);
      localStorage.setItem('cart', JSON.stringify(getStoredData));
      // product.status = this.status;
      this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
    } else {
      let index = -1;
      for (let i = 0; i < this.cartItems.length; i++) {

        if (id === this.cartItems[i].id) {
          this.cartItems[i].cartProdQnt = product.cartProdQnt;
          index = i;
          break;
        }
      }
      if (index === -1) {
        this.cartItems.push(product);
        localStorage.setItem('cart', JSON.stringify(this.cartItems));
      } else {
        localStorage.setItem('cart', JSON.stringify(this.cartItems));
      }
    }
    this.cartNumberFunc();
  }

  cartNumberFunc(): void {
    let cartValue: [] = [];
    cartValue = JSON.parse(localStorage.getItem('cart') || '[]');
    this.cartNumber = cartValue.length;
    this.dataService.cartRefresh$.next(this.cartNumber);
    console.log('cart Number:' + this.cartNumber);
  }

}
