import { FormControl, FormGroup, Validators, FormControlName } from '@angular/forms';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-user-data',
  templateUrl: './user-data.component.html',
  styleUrls: ['./user-data.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class UserDataComponent implements OnInit {
  formUserData!: FormGroup;
  user: any;
  name;
  phone;
  address;
  longitude;
  latitude;
  coordinates = [];
  constructor(private data: DataService) {
    this.data.cartRefresh$.subscribe((info: any) => {
      this.user = JSON.parse(localStorage.getItem('user') || '[]');
      console.log('pla', this.coordinates);
      this.getPosition();
      this.addForm();
    });
  }

  ngOnInit(): void {
    this.user = JSON.parse(localStorage.getItem('user') || '[]');
    console.log('pla', this.coordinates);
    this.getPosition();

    this.addForm();

  }
  getPosition(): void {
    navigator.geolocation.getCurrentPosition(resp => {
      console.log(resp.coords.latitude, resp.coords.longitude);
      console.log(resp);



      this.coordinates = [resp.coords.latitude, resp.coords.longitude]
    });

  }
  addForm(): void {
    this.formUserData = new FormGroup({
      name: new FormControl(this.user.name, { validators: [Validators.required] }),
      phone: new FormControl(this.user.phone, { validators: [Validators.required] }),
      address: new FormControl(this.user.address, { validators: [Validators.required] }),
      coordinates: new FormControl(this.coordinates, { validators: [Validators.required] }),
    });
  }
  saveUser(): void {
    localStorage.setItem('user', JSON.stringify(this.formUserData.value));
    const user = JSON.parse(localStorage.getItem('user') || '[]');
    this.data.cartRefresh$.next(user);
  }
}
