import { DataService } from './../../services/data.service';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import {
  MatSnackBar,
  MatSnackBarHorizontalPosition,
  MatSnackBarVerticalPosition,
} from '@angular/material/snack-bar';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class HeaderComponent implements OnInit {
  cartItems: any = [];
  count = 0;
  cartNumber = 0;
  horizontalPosition: MatSnackBarHorizontalPosition = 'start';
  verticalPosition: MatSnackBarVerticalPosition = 'bottom';
  constructor(private data: DataService, private snackBar: MatSnackBar) {
    this.data.cartRefresh$.subscribe((info: any) => {
      this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
      this.count = info;
    });
  }

  ngOnInit(): void {
    this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
    console.log(this.cartItems);
    this.cartItemFunc();
  }

  deleteCart(): void {
    localStorage.removeItem('cart');
    this.cartNumberFunc();
    this.snackBar.open(`Cart Deleted Successfully`, 'End now', {
      duration: 3000,
      panelClass: ['panel', 'main-color'],
      horizontalPosition: this.horizontalPosition,
      verticalPosition: this.verticalPosition,
    });
  }

  cartItemFunc(): void {
    if (localStorage.getItem('cart') != null) {
      this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
      this.count = this.cartItems.length;
    }
  }

  removeOneItem(productId: any): void {
    this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
    for (let i = 0; i < this.cartItems.length; i++) {
      if (this.cartItems[i]._id === productId) {
        this.snackBar.open(
          ` ( ${this.cartItems[i].name} )   Deleted Successfully`,
          'End now',
          {
            duration: 3000,
            panelClass: ['panel', 'main-color'],
            horizontalPosition: this.horizontalPosition,
            verticalPosition: this.verticalPosition,
          }
        );
        this.cartItems.splice(i, 1);
        localStorage.setItem('cart', JSON.stringify(this.cartItems));
        this.cartNumberFunc();
        // this.loadCart();
        this.check();
        if (this.cartItems.length === 0) {
          localStorage.removeItem('cart');
        }
      }
    }
  }
  check(): void {
    this.cartItems = JSON.parse(localStorage.getItem('cart') || '[]');
  }

  cartNumberFunc(): void {
    let cartValue: [] = [];
    cartValue = JSON.parse(localStorage.getItem('cart') || '[]');
    this.cartNumber = cartValue.length;
    this.data.cartRefresh$.next(this.cartNumber);
    console.log('cart Number:' + this.cartNumber);
  }
}
